const ProfileImage = chakra(Image, {
    shouldForwardProp: prop => ['width', 'height', 'src', 'alt'].includes(prop)
  })
const avatar_src = "https://avatars.githubusercontent.com/u/73838171?s=400&u=33260630566903ad3080dce078d872de8b92ff42&v=4"
const RouteChange = (opt) => {
    let GithubPage = 'https://github.com/Maxwell-Wong';
    let CSDNPage = 'https://blog.csdn.net/m0_52387305';
    let Email = 'mailto:78146185@qq.com';
    if (opt == "github") {
        window.open(GithubPage,'_blank', 'noopener,noreferrer')
    }
    else if (opt == "email") {
        window.open(Email,'_blank', 'noopener,noreferrer')
    }
    else if (opt == "csdn") {
        window.open(CSDNPage,'_blank', 'noopener,noreferrer')
    }
        
}

export function Home(props) {
    const { isOpen, onToggle } = useDisclosure();
    const bg = useColorModeValue('teal.200', 'teal.500')
    const [like, setLike] = useState(0)
    const handleLike = (e) => {
        setLike(like + 1)
    }
    const handleConfirm = () => {
        if (window.confirm("Do you really want to leave?")) {
            window.alert("Yes!")
        }
    }
    return (
        <Container maxW='container.sm' textAlign="center" centerContent padding="2" >
           
            <Layout />
           
            <Box display={{md:'flex'}} mt="100"> 
                <Box marginRight="50">
                <Heading>Maxwell Wong</Heading>
                    <Text as='u'>Sun Yat-sen University(SYSU)</Text><br/>
                    <Text as='u'>School of Computer Science</Text>
                </Box>
            
                <Box
                 borderColor="whiteAlpha.800"
                 borderWidth={2}
                 borderStyle="solid"
               
                 display="inline-block"
                 borderRadius="full"
                 overflow="hidden"
                >
                <Avatar src={avatar_src} size="2xl" borderColor="whiteAlpha.800"/>
                </Box>
                
            </Box>
            
            <Section delay={0.3}>
                <Heading as="h3"  size='lg' variant="section-title">
                Interests
                </Heading>
                    <Paragraph>
                        Hi there! I'm Maxwell Wong, or Jingyun Huang(His/He),
                        Welcome to my blog!
                        I'm extremely interested in Neuroscience, Artificial Intelligence and love Running and Traveling.
                        There is a special moment, I have to mention, when I was still in middle school,
                        I took part in the Brain Bee competition, which was an opportunity for me to love and dive into this exciting subject.
                        As a CSer, what I am concentrating on is Brain-Computer Interface(BCI), Artificial Intelligence(AI) and Computational Neuroscience(CN).

                    </Paragraph>
                <Box align="center" my={4}>
                        <Text mb="3px">: {like}</Text>
                        <Button bgColor={bg} onClick={handleLike}>Like</Button>    
                </Box>
            </Section>
        
            <Section delay={0.4}>
                <Heading as="h3"  size='lg' variant="section-title">
                Posts
                </Heading>
                    <Paragraph>
                    Hey! I'm Maxwell Wong, welcome to my blog. Click the button below, enter my blog page.
                    </Paragraph>
                <Box align="center" my={4}>
                    <Button bgColor={bg}><Link href={`${variables['SERVER_URL']}/blog`} >My Posts</Link></Button>
                </Box>
            </Section>

            <Section delay={0.4}>
                <Heading as="h3"  size='lg' variant="section-title">
                Find More 
                </Heading>
                <Paragraph>
                    For more infomation, access to:
                    <Box mt="5">
                        <Button leftIcon={<FaArrowRight/>}  onClick={()=>RouteChange('csdn')}>CSDN @Maxwell-Wong</Button>
                    </Box>
                    <Box mt="5">
                        <Button leftIcon={<FaGithub/>}   onClick={()=>RouteChange('github')}>Github @Maxwell-Wong</Button>
                    </Box>
                </Paragraph>
            </Section>
                
            <Collapse in={isOpen} animateOpacity>
                <Box
                p='40px'
                color='white'
                mt='4'
                bg='teal.500'
                rounded='md'
                shadow='md'
                >
                Hey!
                </Box>
            </Collapse>
            
            <Box align="center" w="100%" mb="3">
                <Badge variant='outline' colorScheme='cyan'>@2022 Maxwell-Wong</Badge>
                <Text>All Rights Reserved.</Text>  
            </Box>
        </Container>
    )
}
